import React, { Component } from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import DeleteIcon from '@material-ui/icons/Delete';
import SearchIcon from '@material-ui/icons/Search';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';
import FavoriteIcon from '@material-ui/icons/Favorite';
import './01.css'
// bug 修改和（删除以及搜索）不能交叉进行（未处理）
let stars,arr,index;
let temp=localStorage.getItem("todos");
if(temp){
  arr=JSON.parse(temp);
}else{
  arr=[
    {name:"默认todo",index:0},
    {name:"默认todo",index:1},
    {name:"默认todo",index:2},
  ];
  localStorage.setItem("todos",JSON.stringify(arr));
}
let temp0=localStorage.getItem("star");
let temp1=localStorage.getItem("index");
stars=temp0?JSON.parse(temp0):(localStorage.setItem("star",JSON.stringify([-1]),[-1]));
index=temp1?JSON.parse(temp1):(localStorage.setItem("index",JSON.stringify(3),3));

export default class BasicTextFields extends Component {
    constructor(props){
        super(props);
        console.log(props);
        this.state={
        p:arr||props.t,
        add:'',
        index:index||3,
        revise:true,
        modifyindex:'',
        back:'',
        timer:'',
        star:stars||[-1],
        }
    }
    gettodos=(e)=>{
      let arr=JSON.parse(localStorage.getItem("todos"));
      let stars=JSON.parse(localStorage.getItem("star"));
      let index=JSON.parse(localStorage.getItem("index"));
      this.state.p=arr;
      this.state.star=stars;
      this.state.index=index;
    }
    settodos=(e)=>{
      localStorage.setItem("todos",JSON.stringify(this.state.p));
      localStorage.setItem("star",JSON.stringify(this.state.star));
      localStorage.setItem("index",JSON.stringify(this.state.index));
    }
    f5=(e)=>{
      this.setState({});
    }
    back=(e)=> {
        let arr=localStorage.getItem("todos");
        arr=arr?arr=JSON.parse(arr):'';
        this.state.back='';
        console.log(e);
        this.state.p=arr||this.state.p;
        this.state.temp='';
        this.f5();
    }
    model=(e)=>{
      this.setState({
        add:e.target.value,
      });
    }
    bridge=(e)=>{
      if(this.state.back){
        this.back();
      }else{
        this.puls();
      }
    }
    puls=()=>{
      if(this.state.revise){
          this.state.p.push({
            name:this.state.add?this.state.add:"默认todo",
            index:this.state.index++,
           });
      }else{
        this.state.p.some(r=>{
          if(r.index==this.state.modifyindex){
            r.name=this.state.add;
            return true;
          }else{
            return false
          }
        })
        this.state.modifyindex=-1;
        this.state.revise=true;
        if(this.state.temp)
        this.state.back="Back";
      }
      this.state.add='';
      this.settodos();
      this.f5();
    }
    modify=(e,i)=>{
      return (t)=>{
          this.gettodos();
          this.state.temp=this.state.back;
          this.state.back='';
          this.state.revise=false;
          this.state.modifyindex=e.index;
          this.state.add=e.name;
          this.inSearch();
          this.f5();
      }
    }
    favor=(e,i)=>{
      return (t)=>{
        t.stopPropagation();
        this.gettodos();
        this.state.p=this.state.p.filter(r=>r.index!=e.index);
        this.state.p=[e,...this.state.p];
        let temp=this.state.star;
        if(!(temp.some(r=>r==e.index)))
        temp.push(e.index);
        this.state.star=temp;
        // console.log("f",e.index,e);
        this.settodos();
        this.inSearch();
        this.f5();
      }
    }
    unfavor=(e,i)=>{
      return (t)=>{
        t.stopPropagation();
        // console.log("uf",e.index,e);
        this.gettodos();
        // let arr=[];
        // Object.assign(arr,this.state.p);
        this.state.p=this.state.p.filter(r=>r.index!=e.index);
        console.log(this.state.p);
        this.state.p.push(e);
        // this.state.p=arr;
        this.state.star=this.state.star.filter(r=>r!=e.index);
        this.settodos();
        this.inSearch();
        this.f5();
      }
    }
    inSearch=(e)=>{
      if(this.state.back||e){
        this.search();
      }
    }
    compare=(a)=>{
      // console.log(a in this.state.star);
      return  (!this.state.star.some(r=> r==a))
    }
    t=(e)=>{
      e.stopPropagation();
      this.gettodos();
      this.state.p=this.state.p.filter((r)=>r.index!=e.currentTarget.getAttribute("data-index"));
      localStorage.setItem("todos",JSON.stringify(this.state.p));
      this.inSearch();
      this.f5();
    }
    search=(e)=>{
      localStorage.setItem("todos",JSON.stringify(this.state.p));
      let arr=this.state.p.filter(r=>r.name.includes(this.state.add));
      this.state.back="Back";
      this.state.p=arr;
      this.f5();
    }
render(){
    return (
    <div >
    <form  noValidate autoComplete="off" className="top">
      <TextField id="standard-basic" label="to-dos" value={this.state.add} onChange={this.model}/>
      <Button
        variant="contained"
        color="primary"
        className="add"
        onClick={this.bridge}
      >
        {this.state.back?"Back":(this.state.revise?"Push":"Turn")}
      </Button>
      <SearchIcon 
      style={{fontSize:'1.3em',transform:"scale(2) translateX(100%) translateY(30%)",color:" rgb(0,190,172)"}}
      onClick={this.search}
      />
    </form>
    <List component="nav" aria-label="secondary mailbox folders">
      {this.state.p.map((r,i)=>(
      <ListItem button key={i} onClick={this.modify(r,i)} className={r.index==this.state.modifyindex?"on":""}>
        <ListItemText primary={r.name} />
        {this.compare(r.index)
        ?<FavoriteBorderIcon className="belefter" style={{color:"pink"}} onClick={this.favor(r,i)}/>
        :<FavoriteIcon className="belefter" style={{color:"pink"}} onClick={this.unfavor(r,i)}/>}
        <DeleteIcon onClick={this.t} data-index={r.index} style={{color:"grey"}} className={r.index==this.state.modifyindex?"no":""}/>
      </ListItem>))}
      {/* {this.state.p.filter(r=>r.index in this.state.star).map((r,i)=>(
        <ListItem button key={i} onClick={this.modify(r,i)}>
        <ListItemText primary={r.name} />
        <FavoriteIcon className="belefter" onClick={this.unfavor(r,i)}/>
        <DeleteIcon onClick={this.t} data-index={r.index}/>
      </ListItem>))
      }
      {
        this.state.p.filter(r=>(!r.index in this.state.star)).map((r,i)=>(
          <ListItem button key={i+this.state.star.length} onClick={this.modify(r,i+this.state.star.length)}>
          <ListItemText primary={r.name} />
          <FavoriteBorderIcon className="belefter" onClick={this.favor(r,i)}/>
          <DeleteIcon onClick={this.t} data-index={r.index}/>
        </ListItem>))
      } */}
    </List>
  </div>
  );
}
}
